.Con {
    width: 100%;
    /* border: 1px solid red; */
  }
  
  .projects {
    margin: auto;
    margin-top: 110px;
    border-radius: 10px;
    padding: 10px;
    width: 340px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
      rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
      rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  }
  .projects h1 {
    font-size: 40px;
    color: aqua;
    margin: 0px;
    letter-spacing: 12px;
    text-shadow: 0 0 5px #1388a8, 0 0 15px #1388a8, 0 0 20px #1388a8,
      0 0 40px #96d7e9, 0 0 60px #31a9d8, 0 0 10px #96d7e9, 0 0 98px #31a9d8;
    color: #fff6a9;
  }
  
  .container {
    width: 1100px;
    /* position: relative; */
    height: 380px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: auto;
    /* border: 1px solid black; */
    margin-top: 80px;
  }
  
  /* .container .card {
    position: relative;
    margin-top: 0px;
  } */
  
  .image {
    width: 280px;
    height: 160px;
    transition: 0.1s;
    margin-top: 10px;
  }
  .clone {
    text-shadow: 0 0 5px #1388a8, 0 0 15px #1388a8, 0 0 20px #1388a8,
      0 0 40px #96d7e9, 0 0 60px #31a9d8, 0 0 10px #96d7e9, 0 0 98px #31a9d8;
    color: #fff6a9;
  }
  .giticon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-bottom: 0px;
    box-sizing: border-box;
    margin-right: 20px;
  }
  .giticon:hover {
    box-shadow: 0 0 5px #1388a8, 0 0 15px #1388a8, 0 0 20px #1388a8,
      0 0 40px #96d7e9, 0 0 60px #31a9d8, 0 0 10px #96d7e9, 0 0 98px #31a9d8;
    background-color: #fff6a9;
  }
  .aboutweb {
    text-align: justify;
  }
  .container .card .face {
    width: 300px;
    height: 200px;
    transition: 0.75s;
  }
  
  .container .card .face.face1 {
    position: relative;
    /* background: #333; */
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    z-index: 1;
    transform: translateY(100px);
  }
  
  .container .card:hover .face.face1 {
    transform: translateY(0);
    box-shadow: inset 0 0 60px #1388a8, inset 20px 0 80px #1388a8,
      inset -20px 0 80px #1388a8, inset 20px 0 300px #96d7e9,
      inset -20px 0 300px #0ff, 0 0 50px #96d7e9, -10px 0 80px #31a9d8,
      10px 0 80px #0ff;
  }
  
  .container .card .face.face1 .content {
    opacity: 0;
    transition: 0.5s;
    text-align: center;
  }
  
  .container .card:hover .face.face1 .content {
    opacity: 1;
  }
  
  .container .card .face.face1 .content i {
    font-size: 3em;
    color: white;
    /* display: inline-block; */
  }
  
  .container .card .face.face1 .content h3 {
    font-size: 2em;
    color: white;
    text-align: center;
  }
  
  .container .card .face.face1 .content a {
    transition: 0.5s;
  }
  
  .container .card .face.face2 {
    /* position: relative; */
    /* background: whitesmoke; */
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    box-sizing: border-box;
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.8);
    transform: translateY(-100px);
    border-radius: 20px;
  }
  
  .container .card:hover .face.face2 {
    transform: translateY(0);
  }
  
  .container .card .face.face2 .content p,
  a {
    font-size: 10pt;
    margin: 0;
    padding: 0;
    color: #c4c4c4;
  }
  
  .container .card .face.face2 .content a {
    text-decoration: none;
    color: black;
    box-sizing: border-box;
    /* outline: 1px dashed #333; */
    align-content: center;
    padding: 10px;
    margin: 15px 0 0;
    display: inline-block;
  }
  
  .container .card .face.face2 .content a:hover {
    /* background: #333; */
    color: whitesmoke;
    /* box-shadow: inset 0px 0px 10px rgba(0, 0, 0, 0.5); */
  }
  
